import React from "react";
import FormLogin from "./FormLogin";
import ProviderLogin from "./ProviderLogin";
import "../css/style.css";

class Login extends React.Component {
  Signup = () => {
    this.props.history.push("/signup");
  };
  render() {
    return (
      <div className="vh-100 bg-secondary mycont1">
        <div className="d-flex justify-content-center">
          <div className="w-25 bg-dark shadow mt-5 p-3 rounded mycont2">
            <ProviderLogin />
            <FormLogin />
          </div>
        </div>
        <div className="d-flex justify-content-center mt-3">
          <p className="text-dark mytext1">Dont have an account? </p>
          <a href="#" className="text-primary" onClick={this.Signup}>
            Sign Up
          </a>
        </div>
      </div>
    );
  }
}

export default Login;
