import React from "react";
import FormSignup from "./FormSignup";
import ProviderSignup from "./ProviderSignup";
import "../css/style.css";

class Signup extends React.Component {
  Login = () => {
    this.props.history.push("/login");
  };
  render() {
    return (
      <div className="vh-100 bg-secondary mycont1">
        <div className="d-flex justify-content-center">
          <div className="w-25 bg-dark shadow mt-5 p-3 rounded mycont2">
            <ProviderSignup />
            <FormSignup />
          </div>
        </div>
        <div className="d-flex justify-content-center mt-3">
          <p className="text-dark mytext1">Already have an account? </p>
          <a href="#" className="text-primary" onClick={this.Login}>
            Sign In
          </a>
        </div>
      </div>
    );
  }
}

export default Signup;
